<template>
	<div>
		<button type="button" @click="isShow = !isShow">显示/隐藏</button>
		<transition>
			<h1 v-show="isShow">你好啊</h1>
		</transition>
		
	</div>
</template>

<script>
	export default{
		name:'Test',
		data(){
			return{
				isShow:true
			}
		},
	}
</script>

<style scoped>
	h1{
		background-color: orange;
	}
	.v-enter-active{
		animation: at 1s linear;
	}
	.v-leave-active{
		animation: at 1s linear reverse;
	}
	@keyframes at{
		from{
			transform: translateX(100%);
		}
		to{
			transform: translateX(0px);
		}
	}
</style>
